<template>
	<div class="usinghelpo-container">
		<div class="usinghelpo-header">
			<div class="usinghelpo-zhong">
				<div class="usinghelpo-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="usinghelpo-ti">使用帮助</div>
				<div></div>
			</div>
		</div>
		<div class="usinghelpo-content">

		</div>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				msg: ''
			}
		},
		methods: {
			back(){
				window.history.back();
			}
		},
		created() {
			// 页面刚加载的时候执行

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.usinghelpo-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.usinghelpo-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.usinghelpo-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.usinghelpo-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.usinghelpo-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.usinghelpo-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
		background-image: url('../assets/bg_sybz2.png');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		height: 100%;
		width: 100%;
	}
</style>


